import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/HomeView.vue')
  },
  {
    path: '/categories',
    name: 'categories',
    component: () => import('../views/CategoriesView.vue')
  },
  {
    path: '/products',
    name: 'products',
    component: () => import('../views/ProductsView.vue')
  },
  {
    path: '/new',
    name: 'newProduct',
    component: () => import('../views/NewProductView.vue')
  },
  {
    path: '/products/:id',
    name: 'product',
    component: () => import('../views/ProductView.vue')
  },
  {
    path: '/pages/contacts',
    name: 'contacts',
    component: () => import('../views/PageContactsView.vue')
  },
  {
    path: '/pages/general',
    name: 'general',
    component: () => import('../views/PageGeneralInfoView.vue')
  },
  {
    path: '/pages/home',
    name: 'homepage',
    component: () => import('../views/PageHomeView.vue')
  },
  {
    path: '/pages/catalog',
    name: 'catalogpage',
    component: () => import('../views/PageCatalogView.vue')
  },
  {
    path: '/pages/cart',
    name: 'cartpage',
    component: () => import('../views/PageCartView.vue')
  },
  {
    path: '/pages/favorites',
    name: 'favoritespage',
    component: () => import('../views/PageFavoritesView.vue')
  },
  {
    path: '/pages/about',
    name: 'about',
    component: () => import('../views/PageAboutView.vue')
  },
  {
    path: '/pages/faq',
    name: 'faq',
    component: () => import('../views/PageFAQView.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return {x: 0, y: 0};
	}
})

export default router
